@import url("~intl-tel-input/build/css/intlTelInput.min.css");
.wcf-select input {
  min-height: 0;
  padding: 0;
  border: 0;
  line-height: 1;
}
.wcf-select input:focus {
  box-shadow: none;
}
.wcf-select svg {
  margin: -4px;
}

.wcf-multi-group,
.wcf-multi-group * {
  box-sizing: border-box;
}

.wcf-multi-group__buttons {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-top: 10px;
}

.wcf-button {
  padding: 0;
  background: transparent;
  border: 0;
  cursor: pointer;
}
.wcf-button svg {
  display: block;
}
.wcf-button--move {
  cursor: move;
  margin-right: 10px;
}
.wcf-button--delete {
  cursor: pointer;
  margin-left: auto;
}

.wcf-multi-group-row {
  background-color: white;
  border: 1px solid #c3c4c7;
}
.wcf-multi-group-row:not(:first-of-type) {
  border-top: none;
}
.wcf-multi-group-row__header {
  background: white;
  border: 0;
  color: #2c3338;
  display: flex;
  font-weight: 400;
  margin: 0;
  padding: 1em 1.5em;
  min-height: 46px;
  position: relative;
  text-align: left;
  align-items: center;
  justify-content: space-between;
  user-select: auto;
}
.wcf-multi-group-row__title {
  font-weight: bold;
  cursor: pointer;
}
.wcf-multi-group-row__content {
  margin: 0;
  padding: 1em 1.5em;
  background: white;
}
.wcf-multi-group-row__content--collapsed {
  height: 0;
  overflow: hidden;
  padding: 0;
}
.wcf-multi-group-row__content-item:not(:last-of-type) {
  margin-bottom: 10px;
}
.wcf-multi-group-row__content-item-label {
  margin-bottom: 5px;
  display: block;
}

.iti__flag-container {
  z-index: 5;
}

.wcf-post-selected__item-header {
  display: flex;
  flex-flow: row nowrap;
}
.wcf-post-selected__item {
  background-color: white;
  padding: 10px 15px;
  margin: 5px 0;
  border: 1px solid #c3c4c7;
}
.wcf-post-selected__item p {
  font-size: 12px;
  line-height: 1.3;
  max-height: 34px;
  text-overflow: ellipsis;
  overflow: hidden;
}

.wcf-sortable-ghost {
  background: rgba(255, 255, 255, 0.3);
  border: 1px dashed #c3c4c7;
}

.wcf-sortable-ghost * {
  visibility: hidden;
}

.wcf-sortable-drag {
  border: 1px solid #c3c4c7;
}

.wcf-sortable-drag .wcf-button--move {
  cursor: grab;
}

.wcf-attachment {
  display: inline-flex;
  background-color: white;
  padding: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 350px;
}
.wcf-attachment__image {
  margin-right: 20px;
}
.wcf-attachment__image img {
  display: block;
}
.wcf-attachment .wcf-button--move,
.wcf-attachment .wcf-button--delete {
  align-self: flex-start;
}

.wcf-error {
  border: 1px solid #DC3232;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.5);
  color: #DC3232;
  border-radius: 3px;
}

.wcf-block {
  border: 1px solid rgba(0, 0, 0, 0.25);
  padding: 10px 15px;
  position: relative;
  border-radius: 3px;
}
.wcf-block__title {
  position: absolute;
  top: -17px;
  right: 20px;
  height: 34px;
  background: white;
  border: 1px solid rgba(0, 0, 0, 0.25);
  padding: 0 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  z-index: 2;
}